import React, {  } from 'react'
import { motion } from 'framer-motion'

export default function Card({time, weather}) {
  // console.log(props)

  // console.log(time)
  // console.log(weather)
  const data = weather.list[0]
  
  // const [data, setData] = useState(weather.list[0])
  
  // useEffect(() => {
  //   console.log("chanched");

  // },[weather])
        

  return (

    <motion.div className='card'
    
    initial={{
      x:"100vw",
      scale:.5
    }}
    animate={{
      x:0,
      scale:1
    }}
    
    exit={{
      x:"-100vw",
      scale:.5
      // positiom:"absolute"
    }}
    
    >
      <div className='heading'> 
        <h3>{data.name} .{data.sys.country}</h3>
        <p>{data.weather[0].description} | {time.hour > 12 ? time.hour - 12 : time.hour}:{time.minute < 10 ? `0${time.minute}`: time.minute}</p>
      </div>

      <div className='mid-row'>
        <h1> {Math.round(data.main.temp)} </h1>
      <div
        className="weather-icon"
        style={{
          backgroundImage:`url(http://openweathermap.org/img/wn/${data ? data.weather[0].icon: "10d"}@2x.png)`
        }}
      />        
      </div>


    </motion.div>
  )
}
