import React, {useEffect, useRef, useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { Temporal } from "@js-temporal/polyfill"
import Card from './Card';


export default function Weather() {

  const [city, setCity] = useState("riyadh");
  const [weather, setWeather] = useState("Loding")
  const [suggestions, setSuggestions] = useState([])
  const [rerender, setRerender] = useState(false)

  const cityInput = useRef()

  useEffect(() => {
     fetch(`https://api.openweathermap.org/data/2.5/find?id=524901&units=metric&country=sa&lang=ar&q=${city}&appid=6362937d262cd8b3ffed305bf38c075d`)
      .then((res) =>  {   return res.json()})
      .then((res) => {
        res.list.length > 0 ? setWeather(res) : setWeather(false)
        setRerender(!rerender)
      })
      .catch(res => {
      console.log(res)
    });

  // eslint-disable-next-line react-hooks/exhaustive-deps
  },[city]);


  // const setHandle = (event) =>  {
  //   setCity(event.target.value)
  // }
  // console.log(motion)

  const cities = ['الرياض','جيزان',"جدة","الباحة",'مكة المكرمة',"م"]
  // const options = cities.map(el => <option value={el} key={el}>{el}</option>)


  const time = new Temporal.Now.plainTimeISO()

  // search function
  const suggestHandle = (event) =>  {

    if(event.target.value.length > 0 ) {
      setSuggestions(prev => {
        let equal = []
        prev.map(el => el.startsWith(event.target.value) && equal.push(el))
        return equal;
      })
      for (let i = 0;i< cities.length;i++) { 
        if (cities[i].startsWith(event.target.value)) {
          if(suggestions.every(e => e !== cities[i])) {
            setSuggestions(prev => [...prev, cities[i]])
          }
        } 
      }
    } else {
      setSuggestions([])
    }
  }

  const cityHandler = event =>  {
    setCity(event.target.textContent)
    setSuggestions([]);
    cityInput.current.value = ""
  }

  function serchButton() {
    if (cityInput.current.value.length > 2) {
      setCity(cityInput.current.value)
      cityInput.current.value = ""
    }
  }

  const errorDiv = <motion.div
                  className='error'
                  key={3}

                  initial={{
                    x:"100vw",
                    scale:.5
                  }}

                  animate={{
                    x:"0",
                    scale:[1, 1.2, 1],
                    y:-10
                  }}
                  
                  exit={{
                    x:"-100vw"
                  }}
                  
                  transition={{
                    scale:{repeat:Infinity}
                  }}
                    >مالقيت شي</motion.div>

  return (
    // <p>{time.minute}</p>
    // <p>{time.hour}</p>
    // {time.year}/{time.month}/{time.day}
    // <select onClick={setHandle} type="select">
    //  {options}
    //</select> 
    
    <div className="container">
      
      <h1>اختار المدينة</h1>
      <div className='input-city'>
        <button onClick={ serchButton }> بحث </button>
      <input ref={cityInput} onKeyPress={(e) => e.key === "Enter" && serchButton()} type="text" placeholder='المدينة' onChange={suggestHandle} />
      </div>
      {suggestions[1] && suggestions.map((el, i) => (
        <motion.p key={el} onClick={cityHandler}  
        className="city"

        style={{
          top: `calc(13.2rem + ${i * 4}rem)`,
        }}

        initial={{
          x:-30,
          opacity:0
        }}
        animate={{
          x:0,
          opacity:1,
        }}
        transition={{
          delay: i * .1,
          type:"just"
        }}
        
      >{el}</motion.p>))}

      <AnimatePresence  exitBeforeEnter={true} >
        
        {/* {weather.list ? weather.list.length > 0 ? <Card time={time} weather={weather} /> : errorDiv : errorDiv } */}
        {weather.list && weather.list.length > 0 && rerender && <Card key={1} time={time} weather={weather} />}
        {weather.list && weather.list.length > 0 && !rerender && <Card key={2} time={time} weather={weather} />}

        {weather === false && errorDiv}
      </AnimatePresence>
     
    </div>
  )
}
// backgroundImage:`url(http://openweathermap.org/img/wn/${weather.list ? weather.list[0].weather[0].icon: "10d"}@2x.png)`
